*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
  }

body {
    font-family: 'Montserrat', sans-serif;
    line-height: 1.6;
    margin: 0;
    min-height: 100vh;
    background-color: #f0f0f0;
}

nav{
    width: 100%;
    position: fixed;
    top:0px;
    background-color: rgb(87, 87, 241);
    color:white;
}

ul{
    list-style-type: none;
}

.list_menu{
    display: flex;
    flex-wrap: wrap;
    justify-content:space-between;
    padding: 20px 30px;
    font-weight: 500;
}

.Name_project{
    display: flex;
    font-size: 1.45em;
    align-items: center;
}
.Name_project a{
    text-decoration: none;
    color: white;
}

.Name_project i{
    width: 35px;
    margin-left:0px
}
.Name_project span{
    padding-left: 10px;
}

.logout{
    font-size: 1.2em;
    text-transform:uppercase;
    margin: auto 0;
}

.logout a{
    text-decoration: none;
    color: white;
}



.contain{
    display: flex;
    flex-direction: column;
    margin-top:80px
}

.search_block{
    display:flex;
    border-bottom:1px solid rgb(219, 219, 219);
    padding:25px 0 25px 13%;
    margin:0 75px 0 75px;
}

.search_lists{
    display: flex;
    position: absolute;
    top: 144px;
    left: 18.5%;
    width: auto;
    flex-direction: column;
    border:1px solid rgb(224, 223, 223);
    background-color: white;
    margin-top:5px;
}

.found_lists{
    display: flex;
    padding: 5px 10px  5px 10px;
    color: rgb(17, 17, 17);
    text-decoration: none;
    border-bottom:1px solid rgb(224, 223, 223);
}

.found_lists img{
    width:50px;
    height: 50px;
    border-radius:50px;
}

.found_lists span{
    padding-left: 10px;
}

.search_block input{
    width: 250px;
    height: 40px;
    font-size: medium;
    border-radius: 7px;
    background-color: white;
    padding-left: 10px;
    color: #929191;
    border:1px solid rgb(233, 230, 230);
}
.search_block button{
    width: 130px; 
    margin-left: 10px;
    border-radius: 10px;
    background-color: rgb(100, 100, 247);
    border:1px solid rgb(100, 100, 247);
    color: white;
    font-size: medium;
}

.button_back{
    width: 130px; 
    margin-left: 10px;
    border-radius: 10px;
    background-color: rgb(100, 100, 247);
    border:1px solid rgb(100, 100, 247);
    color: white;
    font-size: medium;
    height: 40px;
}

.list_cards{
    display: flex;
    flex-wrap:wrap;
    justify-content: space-around;
    margin:40px 0 0 0 ;
    padding: 20px 20px 20px 20px;
}
.cards{
    display: flex;
    width: 370px;
    border:1px solid;
    margin-bottom: 40px;
    padding:20px 10px 20px 10px;
    background-color: white;
    border:1px solid rgb(221, 221, 221);
    border-radius: 5px;
}

.cards img{
    width: 70px;
    height: 70px;
    border:1px solid;
    border-radius: 70px;
    border:1px solid rgb(219, 219, 219)
}

.card_profile{
    display: flex;
    flex-direction: column;;
    margin-left: 20px;
}

.card_profile span{
    font-size: 25px;
}
.card_profile a{
    border:1px solid rgb(233, 232, 232);
    border-radius:7px;
    color: black;
    text-decoration: none;
    margin:15px 0px 0 0px;
    padding: 10px 40px 10px 40px;
    background-color: rgb(228, 228, 247);
    color:blue;
}
.contain{
    display: flex;
    flex-direction: column;
    margin-top:80px
}
.contain_repo{
    display: flex;
    margin-top:40px;
    justify-content: flex-end;
    padding:20px 30px 20px 30px;
}

.repo_profile{
    width: 300px;
    display: flex;
    position: fixed;
    left:30px;
    flex-direction: column;
    border:1px solid rgb(212, 212, 212);
    border-radius: 10px;
    background-color: white;
}
.back_repo{
    display: flex;
   /* border:1px solid red;*/
    justify-content: center;
    padding-bottom: 75px;
    background-color: rgb(87, 87, 241);
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}
.back_repo img{
    width: 100px;
    height: 100px;
    position: absolute;
    margin-top: 20px;
    border-radius: 70px;
}

.name_profile{
    display: flex;
    margin-top: 60px;
    flex-direction: column;
    align-items: center;
}
.name_profile span:nth-child(1){
    font-weight: 600;
}

.name_profile span:nth-child(2){
    font-weight: 200;
    color:rgb(75, 74, 74);
}

.profile_follow{
    display: flex;
    justify-content: center;
    align-items: center;
   
    margin-top:20px;
    font-size: 13px;
    color:rgb(75, 74, 74);
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    padding-bottom:10px;
}

.profile_follow span{
    padding-left: 10px;
}


.repo_git{
    display: flex;
    flex-direction: column;
    width: 940px;
    padding-bottom: 100px;
}

.search_repo{
    display: flex;
    flex-direction: column;
    border-bottom:1px solid rgb(207, 207, 207);
    padding:20px 22px 20px 22px;
}

.search_repo input{
    width: 300px;
    height: 40px;
    border-radius: 5px;
    color: #7a7a7a;
    border:1px solid rgb(233, 230, 230);
    font-size: medium;
    border-radius: 7px;
    background-color: white;
    padding-left: 10px;
}

.search_repo button{
    width: 130px; 
    margin-left: 10px;
    border-radius: 10px;
    background-color: rgb(100, 100, 247);
    border:1px solid rgb(100, 100, 247);
    color: white;
    font-size: medium;
    padding:10px 0;
}

.search_lists_repo{
    display: flex;
    position: absolute;
    top: 200px;
    left: 29.6%;
    width: auto;
    flex-direction: column;
    border:1px solid rgb(224, 223, 223);
    background-color: white;
    margin-top:5px;
}

.found_lists_repo{
    display: flex;
    padding: 5px 10px  5px 10px;
    color: rgb(17, 17, 17);
    text-decoration: none;
    border-bottom:1px solid rgb(224, 223, 223);
}

.found_lists_repo span{
    padding-left: 10px;
}

.list_repositories{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 30px;
    padding: 0 0 30px 0px;
}

.repositories{
    display: flex;
    flex-direction: column;
    width:460px;
    border:1px solid rgb(207, 207, 207);
    padding: 15px 0 15px 0;
    margin-bottom: 20px;
    background-color: white;
    border-radius: 5px;
}

.detail_repositories{
    display: flex;
    /*border:1px solid;*/
    justify-content: space-between;
    align-items: center;
    padding: 0 10px 0 10px
}

.detail_repositories span:nth-child(1){
    font-weight: bold;
    font-size: 12px;
    color: rgb(87, 87, 241);
}

.detail_repositories span:nth-child(2){
    font-size: 12px;
    border:1px solid rgb(207, 207, 207);
    padding: 0 4px 0 4px;
    border-radius: 7px;
    color:  rgb(107, 106, 106);;
}

.skill_repositories{
    display: flex;
    justify-content: space-between;
    font-size: 12px;
    margin-top: 55px;
    padding:0 10px;
}

.skill_repositories span{
    color:  rgb(107, 106, 106);;
}
